const ImagesBlockWrapper = styled.div`
    > * + * {
        margin-top: 1em;
    }
`;
const VideoBlock = styled.div`
    background-color: #eee;

    video, img {
        max-width: 640px;
        margin: 0 auto;
        display: block;
    }
`;
const ImagesBlockHeader = styled.h3`
    color: rgba(0, 0, 0, .5);
    font-size: .75em;
    line-height: 1;
    padding-bottom: .25em;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    margin-bottom: 1em;
`;

function ImagesBlock(props: {imageGroup: ImageGroup; max?: number}) {
    const {imageGroup} = props;

    return (
        <ImagesBlockWrapper>
            {/* <ImagesBlockHeader>{props.title}</ImagesBlockHeader> */}

            {
                imageGroup.gfycatId
                ?
                <VideoBlock>
                    <video
                        autoPlay={false}
                        playsInline={false}
                        preload="auto"
                        poster={`https://thumbs.gfycat.com/${imageGroup.gfycatId}-mobile.jpg`}
                        tabIndex={-1}
                        // width={1600}
                        // height={900}
                        controls={true}
                    >
                        <source src={`https://thumbs.gfycat.com/${imageGroup.gfycatId}-mobile.mp4`} type="video/mp4" />
                        <source src={`https://giant.gfycat.com/${imageGroup.gfycatId}.webm`} type="video/webm" />
                        <source src={`https://giant.gfycat.com/${imageGroup.gfycatId}.mp4`} type="video/mp4" />
                        <source src={`https://thumbs.gfycat.com/${imageGroup.gfycatId}-mobile.mp4`} type="video/mp4" />
                    </video>
                </VideoBlock>
                :
                imageGroup.gif
                &&
                <VideoBlock>
                    <img src={imageGroup.gif} alt={imageGroup.gif} />
                </VideoBlock>
            }

            <ImagesGrid>
                {
                    imageGroup.paths.slice(0, props.max || imageGroup.paths.length).map((imgPath, i) => {
                        return (
                            <div key={imgPath}>
                                <img
                                    loading="lazy"
                                    src={`${imageGroup.basePath}${imgPath}`}
                                    width={300}
                                    height={300}
                                />
                            </div>
                        );
                    })
                }
            </ImagesGrid>
        </ImagesBlockWrapper>
    );
}

interface ImageGroup {
    title: string;
    basePath: string;
    paths: string[];
    gfycatId?: string;
    gif?: string;
    Content?: () => JSX.Element;
}

const imageGroups: ImageGroup[] = [
    {
        title: "Photography",
        basePath: '/images/photos/',
        // gfycatId: 'LikableWebbedFly',
        gif: 'https://i.giphy.com/media/v1.Y2lkPTc5MGI3NjExem0wNHVyZXJpZmJscmd4Y3FvZHhpcWp0eGVkd3BpZjZzYTZ3b2VtZCZlcD12MV9pbnRlcm5hbF9naWZfYnlfaWQmY3Q9Zw/V1qPvC3o1qfuQetAM8/giphy-downsized-large.gif',
        paths: photosNature,
    },
];

export default function PhotographyBlock(props: {maxImages?: number; content?: React.FC}) {
    const {content: Content} = props;

    return (
        <div>
            <div style={{ backgroundColor: '#fff' }}>
                <div style={{  }}>
                    <TextBlock>
                        <h2>Photography</h2>

                        {
                            imageGroups.map(imageGroup => {
                                return (
                                    <ImagesBlock
                                        key={imageGroup.title}
                                        imageGroup={imageGroup}
                                        max={props.maxImages}
                                    />
                                );
                            })
                        }

                        {
                            Content
                            &&
                            <Content />
                        }
                    </TextBlock>
                </div>
            </div>
        </div>
    );
}
